import React, { useEffect, useMemo, useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Wrench, CheckCircle, Clock, Trash2, Shield, Eye, EyeOff, X } from 'lucide-react';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { db } from '@/firebase';
import { addDoc, collection, deleteDoc, doc, onSnapshot, orderBy, query, serverTimestamp, updateDoc, where, getDocs, limit } from 'firebase/firestore';
import { ProfitService } from '@/utils/profitService';
import { useShop } from '@/hooks/useShop';
import { userDataService, telegramNotifier } from '@/integrations/firebase/services';
import { MasterPinDialog } from '@/components/MasterPinDialog';

type MaintenanceItemStatus = 'in_progress' | 'completed';

interface MaintenanceItem {
  id: string;
  productName: string;
  faultType?: string;
  customerName?: string;
  mobile?: string;
  repairPrice: number; // سعر التصليح
  wholesalePrice?: number; // سعر الجملة
  profit?: number; // الأرباح (تحسب: سعر التصليح - سعر الجملة)
  profitAdded?: boolean; // تم تسجيل الربح في التقارير
  partialPayment?: number;
  remainingDue?: number;
  status: MaintenanceItemStatus;
  createdAt: Date;
  completedAt?: Date;
  userId: string;
}

interface Props {
  open: boolean;
  onOpenChange: (open: boolean) => void;
}

const MaintenanceDialog: React.FC<Props> = ({ open, onOpenChange }) => {
  const { user } = useFirebaseAuth();
  const { shopId: shopIdCtx } = useShop() || ({} as any);

  const [items, setItems] = useState<MaintenanceItem[]>([]);
  const [productName, setProductName] = useState('');
  const [faultType, setFaultType] = useState('');
  const [customerName, setCustomerName] = useState('');
  const [mobile, setMobile] = useState('');
  const [repairPrice, setRepairPrice] = useState<string>('');
  const [wholesalePrice, setWholesalePrice] = useState<string>('');
  const [partialPayment, setPartialPayment] = useState<string>('');
  // لا توجد خانة منفصلة لسعر الزبون؛ سعر الزبون هو سعر التصليح
  const [showProfit, setShowProfit] = useState<boolean>(false);
  const [pinDialogOpen, setPinDialogOpen] = useState<boolean>(false);
  const [formVisible, setFormVisible] = useState<boolean>(false);

  // تنسيق المبالغ
  const formatMoney = (n: number) => `${new Intl.NumberFormat('ar-EG', { minimumFractionDigits: 2, maximumFractionDigits: 2 }).format(Number(n || 0))} ج.م`;

  // حساب نوافذ التصفير المطلوبة لعرض أرباح الصيانة
  const getDailyWindowStart = () => {
    const now = new Date();
    const start = new Date(now);
    start.setHours(2, 0, 0, 0); // الساعة 2 صباحاً اليوم
    // إن كان الوقت الحالي قبل 2 صباحاً، نرجع لبداية نافذة أمس الساعة 2
    if (now.getTime() < start.getTime()) {
      start.setDate(start.getDate() - 1);
    }
    return start;
  };

  const getMonthlyWindowStart = () => {
    const now = new Date();
    const start = new Date(now.getFullYear(), now.getMonth(), 1, 0, 0, 0, 0); // أول يوم من الشهر 12 منتصف الليل
    return start;
  };

  const dailyWindowStart = getDailyWindowStart();
  const monthlyWindowStart = getMonthlyWindowStart();

  // تجميع أرباح الصيانة المكتملة داخل النوافذ الزمنية
  const maintenanceDailyProfit = items
    .filter(it => it.status === 'completed' && it.completedAt && (it.completedAt.getTime() >= dailyWindowStart.getTime()))
    .reduce((sum, it) => sum + (typeof it.profit === 'number' ? it.profit : 0), 0);

  const maintenanceMonthlyProfit = items
    .filter(it => it.status === 'completed' && it.completedAt && (it.completedAt.getTime() >= monthlyWindowStart.getTime()))
    .reduce((sum, it) => sum + (typeof it.profit === 'number' ? it.profit : 0), 0);

  // الاشتراك الفوري لعناصر الصيانة من Firestore للمستخدم الحالي
  useEffect(() => {
    if (!user?.uid) return;

    const handleSnap = (snap: any) => {
      const list: MaintenanceItem[] = snap.docs.map((d: any) => {
        const data = d.data() as any;
        return {
          id: d.id,
          productName: data.productName,
          faultType: data.faultType,
          customerName: data.customerName,
          mobile: data.mobile,
          // في حال كانت البيانات القديمة تحتوي على customerPrice فقط
          repairPrice: Number((data.repairPrice ?? data.customerPrice ?? 0)),
          wholesalePrice: data.wholesalePrice !== undefined ? Number(data.wholesalePrice) : undefined,
          profit: data.profit !== undefined ? Number(data.profit) : undefined,
          partialPayment: data.partialPayment !== undefined ? Number(data.partialPayment) : undefined,
          remainingDue: data.remainingDue !== undefined
            ? Number(data.remainingDue)
            : (data.partialPayment !== undefined
              ? Math.max(0, Math.round(((Number(data.repairPrice ?? data.customerPrice ?? 0)) - Number(data.partialPayment)) * 100) / 100)
              : undefined),
          profitAdded: !!data.profitAdded,
          status: (data.status as MaintenanceItemStatus) || 'in_progress',
          createdAt: data.createdAt?.toDate?.() || new Date(),
          completedAt: data.completedAt?.toDate?.(),
          userId: data.userId || user.uid,
        };
      });
      setItems(list);
    };

    // المحاولة الأولى: مع ترتيب حسب createdAt (أفضلية)
    const q1 = query(
      collection(db, 'maintenance_items'),
      where('userId', '==', user.uid),
      orderBy('createdAt', 'desc')
    );

    let fallbackUnsub: (() => void) | null = null;
    const unsub = onSnapshot(q1, handleSnap, (err) => {
      console.warn('Failed to subscribe with ordering, falling back without orderBy:', err);
      try {
        const q2 = query(
          collection(db, 'maintenance_items'),
          where('userId', '==', user.uid)
        );
        fallbackUnsub = onSnapshot(q2, handleSnap, (err2) => {
          console.warn('Failed to subscribe maintenance items (fallback):', err2);
        });
      } catch (e) {
        console.warn('Failed to start fallback subscription:', e);
      }
    });

    return () => {
      try { unsub(); } catch {}
      try { if (fallbackUnsub) fallbackUnsub(); } catch {}
    };
  }, [user?.uid]);

  const resetForm = () => {
    setProductName('');
    setFaultType('');
    setCustomerName('');
    setMobile('');
    setRepairPrice('');
    setWholesalePrice('');
    setPartialPayment('');
  };

  const addItem = async (status: MaintenanceItemStatus) => {
    // تأكيد تسجيل الدخول قبل الإضافة
    if (!user?.uid) {
      alert('يرجى تسجيل الدخول قبل إضافة عنصر الصيانة');
      return;
    }
    const numericRepair = Number(repairPrice);
    const numericWholesale = wholesalePrice ? Number(wholesalePrice) : undefined;
    const numericPartial = partialPayment ? Number(partialPayment) : undefined;
    if (!productName.trim() || !numericRepair || Number.isNaN(numericRepair) || numericRepair <= 0) {
      alert('يرجى إدخال اسم المنتج وسعر التصليح بشكل صحيح');
      return;
    }
    try {
      const profit = (numericWholesale !== undefined)
        ? Math.round((numericRepair - numericWholesale) * 100) / 100
        : undefined;

      // ابنِ الحمولة بدون أي حقول undefined
      const payload: any = {
        productName: productName.trim(),
        repairPrice: numericRepair,
        status,
        createdAt: serverTimestamp(),
        userId: user.uid,
        profitAdded: status === 'completed' ? (profit !== undefined) : false,
      };
      const _fault = faultType.trim();
      const _customer = customerName.trim();
      const _mobile = mobile.trim();
      if (_fault) payload.faultType = _fault;
      if (_customer) payload.customerName = _customer;
      if (_mobile) payload.mobile = _mobile;
      if (numericWholesale !== undefined) payload.wholesalePrice = numericWholesale;
      if (profit !== undefined) payload.profit = profit;
      if (status === 'completed') payload.completedAt = serverTimestamp();
      if (numericPartial !== undefined) payload.partialPayment = numericPartial;
      if (numericPartial !== undefined) payload.remainingDue = Math.max(0, Math.round((numericRepair - numericPartial) * 100) / 100);

      const docRef = await addDoc(collection(db, 'maintenance_items'), payload);

      // كتابة إلى المجموعات القديمة المستخدمة بواسطة تطبيقات الموبايل/الكمبيوتر لضمان التزامن عبر المنصات
      try {
        const commonFields: any = {
          linkedId: docRef.id,
          productName: payload.productName,
          faultType: payload.faultType,
          customerName: payload.customerName,
          mobile: payload.mobile,
          repairPrice: payload.repairPrice,
          wholesalePrice: payload.wholesalePrice,
          profit: payload.profit,
          partialPayment: payload.partialPayment,
          remainingDue: payload.remainingDue,
          userId: user.uid,
        };
        if (status === 'in_progress') {
          await addDoc(collection(db, 'maintenance_pending'), {
            ...commonFields,
            status: 'in_progress',
            createdAt: serverTimestamp(),
          });
        } else {
          await addDoc(collection(db, 'maintenance_done'), {
            ...commonFields,
            status: 'completed',
            profitAdded: commonFields.profit !== undefined,
            createdAt: serverTimestamp(),
            completedAt: serverTimestamp(),
          });
        }
      } catch (syncErr) {
        console.warn('تعذر مزامنة عنصر الصيانة مع مجموعات الموبايل/الكمبيوتر:', syncErr);
      }

      // تحديث تفاؤلي للقائمة حتى لو تأخر الـ snapshot
      setItems(prev => [{
        id: docRef.id,
        productName: payload.productName,
        faultType: payload.faultType,
        customerName: payload.customerName,
        mobile: payload.mobile,
        repairPrice: payload.repairPrice,
        wholesalePrice: payload.wholesalePrice,
        profit: payload.profit,
        partialPayment: payload.partialPayment,
        remainingDue: payload.remainingDue,
        profitAdded: !!payload.profitAdded,
        status: payload.status,
        createdAt: new Date(),
        completedAt: payload.status === 'completed' ? new Date() : undefined,
        userId: user.uid,
      }, ...prev]);
      // إذا تمت الإضافة كمكتمل، أضف الربح للتقارير
      if (status === 'completed' && profit !== undefined) {
        try { ProfitService.addProfit(profit, user.uid); } catch {}
      }

      // إذا تمت الإضافة كمكتمل، أضف مبلغ التصليح بالكامل إلى رصيد الدرج النقدي
      if (status === 'completed') {
        try {
          const amount = Math.max(0, Number(numericRepair || 0));
          const genericKey = `cashBalance_${user?.uid || 'default'}`;
          const shopKey = `cashBalance_${user?.uid || 'default'}_${shopIdCtx || 'main'}`;
          const saved = localStorage.getItem(shopKey) ?? localStorage.getItem(genericKey) ?? '0';
          const currentCash = parseFloat(saved || '0') || 0;
          const newCash = currentCash + amount;
          try { localStorage.setItem(genericKey, String(newCash)); } catch {}
          try { localStorage.setItem(shopKey, String(newCash)); } catch {}
          try { await userDataService.updateUserData(user.uid, { cashBalance: newCash }); } catch {}
          try { window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: newCash } })); } catch {}
        } catch {}
      }
      resetForm();
      try { if (user?.uid) { const paidPart = (payload.partialPayment !== undefined) ? `، المدفوع مقدمًا ${payload.partialPayment} جنيه` : ''; const mobileText = payload.mobile ? `، رقم تليفون ${payload.mobile}` : ''; await telegramNotifier.send(user.uid, `تم استلام جهاز صيانة باسم ${payload.customerName || 'بدون اسم'}، الجهاز ${payload.productName}${mobileText}، سعر التصليح ${payload.repairPrice} جنيه${paidPart}`); } } catch {}
    } catch (e) {
      console.error('Failed to add maintenance item:', e);
      const err: any = e || {};
      const msgParts = [err.code, err.message].filter(Boolean).join(' — ');
      const msg = msgParts || 'غير معروف';
      alert(`حدث خطأ أثناء إضافة عنصر الصيانة: ${msg}`);
    }
  };

  const markCompleted = async (id: string) => {
    try {
      const item = items.find(i => i.id === id);
      const profit = (item?.wholesalePrice !== undefined)
        ? Math.round(((item.repairPrice || 0) - (item.wholesalePrice || 0)) * 100) / 100
        : undefined;
      const updatePayload: any = {
        status: 'completed',
        completedAt: serverTimestamp(),
        profitAdded: profit !== undefined ? true : item?.profitAdded || false,
      };
      if (profit !== undefined) updatePayload.profit = profit;
      // عند الضغط على تم التصليح: يعتبر أن العميل سدد المبلغ بالكامل
      const fullPaid = Math.round(((item?.repairPrice || 0)) * 100) / 100;
      updatePayload.partialPayment = fullPaid;
      updatePayload.remainingDue = 0;
      await updateDoc(doc(db, 'maintenance_items', id), updatePayload);
      if (profit !== undefined && !item?.profitAdded && user?.uid) {
        try { ProfitService.addProfit(profit, user.uid); } catch {}
      }

      try {
        const amount = Math.max(0, Number(item?.repairPrice || 0));
        const genericKey = `cashBalance_${user?.uid || 'default'}`;
        const shopKey = `cashBalance_${user?.uid || 'default'}_${shopIdCtx || 'main'}`;
        const saved = localStorage.getItem(shopKey) ?? localStorage.getItem(genericKey) ?? '0';
        const currentCash = parseFloat(saved || '0') || 0;
        const newCash = currentCash + amount;
        try { localStorage.setItem(genericKey, String(newCash)); } catch {}
        try { localStorage.setItem(shopKey, String(newCash)); } catch {}
        if (user?.uid) {
          try { await userDataService.updateUserData(user.uid, { cashBalance: newCash }); } catch {}
        }
        try {
          const shopId = (profile as any)?.shopId || shopIdCtx;
          if (shopId) {
            await updateDoc(doc(db, 'dashboardData', shopId), { cashBalance: newCash, lastUpdated: new Date().toISOString() } as any);
          }
        } catch {}
        try { window.dispatchEvent(new CustomEvent('cashBalanceChanged', { detail: { value: newCash } })); } catch {}
      } catch {}

      // مزامنة مع مجموعات تطبيقات الموبايل/الكمبيوتر
      try {
        const commonFields: any = {
          linkedId: id,
          productName: item?.productName,
          faultType: item?.faultType,
          customerName: item?.customerName,
          mobile: item?.mobile,
          repairPrice: item?.repairPrice,
          wholesalePrice: item?.wholesalePrice,
          profit,
          partialPayment: fullPaid,
          remainingDue: 0,
          userId: user?.uid || item?.userId,
          status: 'completed',
          createdAt: item?.createdAt ? item.createdAt : serverTimestamp(),
          completedAt: serverTimestamp(),
          profitAdded: profit !== undefined,
        };
        // أضف إلى مجموعة المكتمل
        await addDoc(collection(db, 'maintenance_done'), commonFields);
        // احذف من مجموعة جاري التصليح إن وجد
        try {
          const q = query(collection(db, 'maintenance_pending'), where('linkedId', '==', id), limit(1));
          const snap = await getDocs(q);
          const pendingDoc = snap.docs[0];
          if (pendingDoc?.id) {
            await deleteDoc(doc(db, 'maintenance_pending', pendingDoc.id));
          }
        } catch (delErr) {
          console.warn('تعذر حذف العنصر من مجموعة جاري التصليح:', delErr);
        }
      } catch (syncErr) {
        console.warn('تعذر مزامنة حالة الصيانة المكتملة مع مجموعات الموبايل/الكمبيوتر:', syncErr);
      }
      try { if (user?.uid) { const mobileText = item?.mobile ? `، رقم تليفون ${item.mobile}` : ''; await telegramNotifier.send(user.uid, `تم تصليح الجهاز ${item?.productName || ''} للعميل ${item?.customerName || 'بدون اسم'}${mobileText}، مبلغ التصليح الكامل ${(item?.repairPrice || 0)} جنيه`); } } catch {}
    } catch (e) {
      alert('حدث خطأ أثناء تحديث حالة الصيانة إلى تم التصليح');
    }
  };

  const removeItem = async (id: string) => {
    try {
      await deleteDoc(doc(db, 'maintenance_items', id));
      // حاول أيضاً حذف النسخ المتزامنة في المجموعات الأخرى
      try {
        const qPending = query(collection(db, 'maintenance_pending'), where('linkedId', '==', id), limit(1));
        const snapPending = await getDocs(qPending);
        if (snapPending.docs[0]?.id) await deleteDoc(doc(db, 'maintenance_pending', snapPending.docs[0].id));
      } catch {}
      try {
        const qDone = query(collection(db, 'maintenance_done'), where('linkedId', '==', id), limit(1));
        const snapDone = await getDocs(qDone);
        if (snapDone.docs[0]?.id) await deleteDoc(doc(db, 'maintenance_done', snapDone.docs[0].id));
      } catch {}
    } catch (e) {
      alert('حدث خطأ أثناء حذف عنصر الصيانة');
    }
  };

  const inProgressItems = items.filter(it => it.status === 'in_progress');
  const completedItems = items.filter(it => it.status === 'completed');

  const computedProfitPreview = (() => {
    const w = wholesalePrice ? Number(wholesalePrice) : undefined;
    const r = repairPrice ? Number(repairPrice) : undefined;
    if (w === undefined || r === undefined) return '';
    const p = Math.round((r - w) * 100) / 100;
    return isNaN(p as any) ? '' : String(p);
  })();

  // حساب أرباح عنصر الصيانة بطريقة مبسطة لتقليل تعقيد JSX
  const computeProfit = (item: MaintenanceItem) => {
    if (typeof item.profit === 'number') return item.profit;
    const repair = item.repairPrice ?? 0;
    const wholesale = item.wholesalePrice ?? 0;
    return Math.round((repair - wholesale) * 100) / 100;
  };

  return (
    <>
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="maintenance-dialog sm:max-w-3xl w-screen sm:w-auto h-[90vh] sm:h-auto overflow-hidden rounded-none sm:rounded-lg p-0 dark:bg-[#121212]">
        <DialogHeader className="sticky top-0 bg-white dark:bg-[#0F0F0F] z-20 border-b border-gray-200 dark:border-[#333] px-4 py-3">
          <div className="flex items-center gap-2">
            <DialogTitle className="flex items-center gap-2">
              <Wrench className="h-4 w-4 text-amber-600" />
              إدارة الصيانة
            </DialogTitle>
            <Button
              size="sm"
              variant="ghost"
              className="ml-auto"
              onClick={() => onOpenChange(false)}
              title="إغلاق"
            >
              <X className="h-4 w-4" />
            </Button>
          </div>
        </DialogHeader>

        
        <div className="px-4 py-3 space-y-3 overflow-y-auto">
        
        <div className="flex items-center justify-between">
          
          <div className="flex items-center gap-2">
            
            <div className="relative">
              <div className={showProfit ? "" : "filter blur-sm pointer-events-none select-none"}>
                <div className="flex items-center gap-2">
                  <span className="text-xs text-gray-700 dark:text-gray-300 font-medium">أرباح الصيانة</span>
                  <Badge variant="secondary" className="text-[10px]">اليوم</Badge>
                  <span className="text-sm text-green-700 dark:text-green-400 font-semibold">{formatMoney(maintenanceDailyProfit)}</span>
                  <Badge variant="secondary" className="text-[10px] ml-2">الشهر</Badge>
                  <span className="text-sm text-green-700 dark:text-green-400 font-semibold">{formatMoney(maintenanceMonthlyProfit)}</span>
                </div>
              </div>
              {!showProfit && (
                <button
                  type="button"
                  onClick={() => setPinDialogOpen(true)}
                  className="absolute inset-0 rounded bg-white/30 dark:bg-black/30 backdrop-blur-sm cursor-pointer"
                  aria-label="أدخل الرقم السري لعرض أرباح الصيانة"
                />
              )}
            </div>

            
            <div className="flex items-center gap-2 z-10">
              {showProfit ? (
                <Button size="sm" variant="ghost" onClick={() => setShowProfit(false)} title="إخفاء الأرباح">
                  <EyeOff className="h-4 w-4" />
                </Button>
              ) : (
                <Button size="sm" variant="ghost" onClick={() => setPinDialogOpen(true)} title="عرض أرباح الصيانة">
                  <Eye className="h-4 w-4" />
                </Button>
              )}
              <Button size="sm" variant="outline" onClick={() => setPinDialogOpen(true)} className="text-xs">عرض أرباح الصيانة</Button>
            </div>
          </div>

          
          <div className="flex items-center gap-2">
            {!formVisible && (
              <Button className="bg-amber-600 hover:bg-amber-700" onClick={() => setFormVisible(true)}>
                إضافة جهاز صيانة
              </Button>
            )}
          </div>
        </div>

        
        {formVisible && (
        <Card className="mb-3">
          <CardHeader className="pb-2">
            <CardTitle className="text-sm">إضافة منتج للصيانة</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2">
            
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-2">
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">اسم المنتج</label>
                <Input value={productName} onChange={e => setProductName(e.target.value)} placeholder="اسم المنتج" />
              </div>
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">نوع العطل - اختياري</label>
                <Input value={faultType} onChange={e => setFaultType(e.target.value)} placeholder="نوع العطل" />
              </div>
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">اسم العميل - اختياري</label>
                <Input value={customerName} onChange={e => setCustomerName(e.target.value)} placeholder="اسم العميل" />
              </div>
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">رقم الموبايل - اختياري</label>
                <Input value={mobile} onChange={e => setMobile(e.target.value)} placeholder="01xxxxxxxxx" />
              </div>
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">سعر التصليح</label>
                <Input value={repairPrice} onChange={e => setRepairPrice(e.target.value)} placeholder="سعر التصليح بالجنيه" />
              </div>
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">سعر الجملة</label>
                <Input value={wholesalePrice} onChange={e => setWholesalePrice(e.target.value)} placeholder="سعر الجملة بالجنيه" />
              </div>
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">دفع جزء من التصليح</label>
                <Input value={partialPayment} onChange={e => setPartialPayment(e.target.value)} placeholder="المبلغ المدفوع مقدمًا" />
              </div>
              
              <div>
                <label className="text-xs text-gray-700 dark:text-gray-300">الأرباح: سعر التصليح - سعر الجملة</label>
                <Input value={computedProfitPreview} readOnly placeholder={showProfit ? '' : 'أدخل الرقم السري لعرض الأرباح'} />
              </div>
            </div>

            <div className="flex items-center gap-2 pt-2">
              <Button className="bg-amber-600 hover:bg-amber-700" onClick={() => addItem('in_progress')}>جاري التصليح</Button>
              <Button className="bg-green-600 hover:bg-green-700" onClick={() => addItem('completed')}>تم التصليح</Button>
              <Button variant="outline" onClick={() => setFormVisible(false)}>إخفاء النموذج</Button>
            </div>
          </CardContent>
        </Card>
        )}

        <Separator />

        
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 mt-3">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm flex items-center gap-2">
                <Clock className="h-4 w-4 text-amber-600" />
                جاري التصليح
                <Badge variant="secondary" className="ml-auto text-[10px]">{inProgressItems.length}</Badge>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 sm:max-h-[280px] sm:overflow-y-auto">
              {inProgressItems.length === 0 ? (
                <div className="text-xs text-gray-500 dark:text-gray-400">لا توجد عناصر قيد التصليح</div>
              ) : (
                inProgressItems.map(item => (
                  <div key={item.id} className="flex items-center justify-between p-2 bg-white dark:bg-[#1A1A1A] rounded border border-gray-200 dark:border-[#333]">
                    <div>
                      <p className="text-sm font-semibold">{item.productName} <span className="text-gray-600 dark:text-gray-300">— {item.repairPrice} جنيه</span></p>
                      <p className="text-[11px] text-gray-600 dark:text-gray-300">
                        {item.faultType || 'بدون'}
                        {item.customerName && <span> • {item.customerName}</span>}
                        {item.mobile && <span> • {item.mobile}</span>}
                      </p>
                      {typeof (item as any).remainingDue === 'number' && (
                        <p className="text-[11px] text-orange-700 dark:text-orange-400">المتبقي على العميل: {(item as any).remainingDue} جنيه</p>
                      )}
                      {showProfit && (
                        <p className="text-[11px] text-green-700 dark:text-green-400">الأرباح المتوقعة: {item.wholesalePrice !== undefined && item.repairPrice !== undefined ? Math.round(((item.repairPrice || 0) - (item.wholesalePrice || 0)) * 100) / 100 : 0} جنيه</p>
                      )}
                    </div>
                    <div className="flex items-center gap-1">
                      <Button size="sm" className="h-7 bg-green-600 hover:bg-green-700" onClick={() => markCompleted(item.id)}>تم التصليح</Button>
                      <Button size="sm" variant="outline" className="h-7" onClick={() => removeItem(item.id)}>
                        <Trash2 className="h-3 w-3" />
                      </Button>
                    </div>
                  </div>
                ))
              )}
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm flex items-center gap-2">
                <CheckCircle className="h-4 w-4 text-green-600" />
                تم التصليح
                <Badge variant="secondary" className="ml-auto text-[10px]">{completedItems.length}</Badge>
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-2 sm:max-h-[280px] sm:overflow-y-auto">
              {completedItems.length === 0 ? (
                <div className="text-xs text-gray-500 dark:text-gray-400">لا توجد عناصر منتهية</div>
              ) : (
                completedItems.map(item => (
                  <div key={item.id} className="flex items-center justify-between p-2 bg-white dark:bg-[#1A1A1A] rounded border border-gray-200 dark:border-[#333]">
                    <div>
                      <p className="text-sm font-semibold">{item.productName} <span className="text-gray-600 dark:text-gray-300">— {item.repairPrice} جنيه</span></p>
                      <p className="text-[11px] text-gray-600 dark:text-gray-300">
                        {item.faultType || 'بدون'}
                        {item.customerName && <span> • {item.customerName}</span>}
                        {item.mobile && <span> • {item.mobile}</span>}
                      </p>
                      {typeof (item as any).partialPayment === 'number' && (
                        <p className="text-[11px] text-emerald-700 dark:text-emerald-400">المدفوع: {(item as any).partialPayment} جنيه</p>
                      )}
                      {typeof (item as any).remainingDue === 'number' && (
                        <p className="text-[11px] text-orange-700 dark:text-orange-400">المتبقي على العميل: {(item as any).remainingDue} جنيه</p>
                      )}
                      {showProfit && (
                        <p className="text-[11px] text-green-700 dark:text-green-400">الأرباح: {computeProfit(item)} جنيه</p>
                      )}
                    </div>
                    <div className="flex items-center gap-1">
                      <Badge className="text-[10px] bg-green-100 text-green-800">مكتمل</Badge>
                      <Button size="sm" variant="outline" className="h-7" onClick={() => removeItem(item.id)} title="حذف">
                        <Trash2 className="h-3 w-3" />
                      </Button>
                    </div>
                  </div>
                ))
              )}
            </CardContent>
          </Card>
        </div>
        
        {/* زر إضافة للصيانة مثبت لأسفل في الموبايل لضمان الظهور */}
        <div className="sm:hidden sticky bottom-0 left-0 right-0 bg-white dark:bg-[#0F0F0F] border-t border-gray-200 dark:border-[#333] p-3 z-20">
          <Button className="w-full bg-amber-600 hover:bg-amber-700" onClick={() => setFormVisible(true)}>
            إضافة جهاز صيانة
          </Button>
        </div>
        </div>

      </DialogContent>
    </Dialog>
    <MasterPinDialog
      open={pinDialogOpen}
      onOpenChange={setPinDialogOpen}
      mode="verify"
      title="تأكيد الرقم السري الرئيسي"
      description="أدخل الرقم السري لعرض خانة الأرباح"
      onSuccess={() => setShowProfit(true)}
    />
    </>
  );
};

export default MaintenanceDialog;
