/**
 * تنسيق المبلغ المالي بالجنيه المصري
 * @param amount المبلغ المراد تنسيقه
 * @returns المبلغ منسقًا
 */
export function formatAmount(amount: number | null | undefined): string {
  // التحقق من صحة القيمة المدخلة
  if (amount === null || amount === undefined || isNaN(Number(amount))) {
    return '0.00';
  }

  return Number(amount).toLocaleString('en-US', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  });
}

// تنسيق رقم عام بأرقام إنجليزية بدون كسور افتراضيًا
export function formatNumberEnglish(value: number | string): string {
  const n = typeof value === 'string' ? Number(value) : value;
  if (!Number.isFinite(n)) return '0';
  return n.toLocaleString('en-US');
}